
import Icon from 'vux/src/components/icon/index.vue'

import { mapActions, mapGetters } from 'vuex'
export default {
name: 'Headsearch',
components: {
Icon
},
data () {
return {
menuShow: true,
hisShow: false,
allClass: [],
hisArr: [],
hotArr: [],
inpKey: ''
}
},
computed: {
...mapGetters([
'history'
])
},
created () {
this.hisArr = this.history.slice()
},
methods: {
toSearch () {
if (this.hisArr.length > 5) {
this.hisArr.pop(0, 1)
}
this.hisArr.unshift(this.inpKey)
this.setHistory(this.hisArr.slice())
},

toMessage () {
this.$router.push({ name: 'userMessage' })
},
openMenu () {
this.menuShow = false
this.hisShow = false
let that = this
this.$axios.get('api/productClass/getProductClassInfo').then(function (data) {
if (data.data.code === '200') {
that.allClass = data.data.data
}
}).catch(function (err) {
console.log(err)
})
},
closeMenu () {
this.menuShow = true
this.hisShow = false
},
openHis () {
this.getHisData()
if (this.hisShow) {
this.hisShow = false
this.inpKey = ''
} else {
this.hisShow = true
}
this.menuShow = true
},
search () {
this.getHisData()
this.hisShow = true
},
getHisData () {
let that = this
this.$axios.get('api/homePage/getRecommendSearch').then(function (data) {
console.log(data)
if (data.data.code === '200') {
that.hotArr = data.data.data
}
}).catch(function (err) {
console.log(err)
})
},
...mapActions([
'setHistory'
])
}
}



