
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

import Scroll from '../../base/scroll/scroll'
import Headsearch from '../../base/headsearch/headsearch.vue'
import Merchandise from '../../base/merchandise/merchandise.vue'
import { mapActions } from 'vuex'

import { scrollPages } from '../../assets/js/mixin'
const list = () => ['', '', '', '', 'Plus']
const list1 = () => ['', '', '', '']
const list3 = () => ['0-49', '50-99', '100-149', '150-200', '200']
export default {
mixins: [scrollPages],
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
Merchandise,
Headsearch,
Scroll
},
name: 'component_name',
data () {
return {
disabled: false,
covers: false,
probeType: 3,
list2: list(),
list1: list1(),
list3: list3(),
index: 0,
isCked2: false,
isCked1: false,
isCked3: false,
checkedIndex: 0,
cover: false,
navShow: true,
chooseIndex: 0,
istop: true,
sortType: 'desc',
sortClass: 'integrated',
priceHigh: 0,
priceLow: 0,
productClassId: 0,
postage: 0,
format: 0,
highLottery: 0,
firstCondition: 'no',
getBarWidth: function (index) {
return (index + 1) * 22 + 'px'
},
goods: [],
apiUrl: '/api/homePage/getListFreeByCondition',
isNoLoad: false
}
},
computed: {
params: {
get () {
return {
sortType: this.sortType,
sortClass: this.sortClass,
priceHigh: this.priceHigh,
priceLow: this.priceLow,
productClassId: this.productClassId,
postage: this.postage,
format: this.format,
highLottery: this.highLottery,
pageNo: this.pageNo,
pageSize: this.pageSize,
firstCondition: this.firstCondition
}
},
set (val) {
return val
}
}
},
created () {
if (this.$route.query.index) {
this.headerTabClick(this.$route.query.index)
this.index = this.$route.query.index
this.isNoLoad = true
}
},
methods: {
headerTabClick (index) {
this.disabled = true
setTimeout(() => {
this.disabled = false
}, 500)
this.pageNo = 1
let TabArr = ['no', 'all', 'more', 'gold', 'plus']
this.sortType = 'desc'
this.sortClass = 'integrated'
this.priceHigh = 0
this.priceLow = 0
this.productClassId = 0
this.postage = 0
this.format = 0
this.highLottery = 0
this.firstCondition = TabArr[index]
this.goods = []
this.getApi()
},

...mapActions([
'setUserNopay'
]),
tap (index) {
this.covers = true
setTimeout(() => {
this.covers = false
}, 400)
this.pageNo = 1
this.goods = []












if (index === 0) {
this.sortType = 'desc'
this.sortClass = 'newest'
this.priceHigh = 0
this.priceLow = 0
this.productClassId = 0
this.postage = 0
this.format = 0
this.highLottery = 0
this.getApi()
this.goods = []
}
if (index === 1) {
if (this.istop) {
this.sortType = 'asc'
this.sortClass = 'price'
this.priceHigh = 0
this.priceLow = 0
this.getApi()
this.goods = []
} else {
this.sortType = 'desc'
this.sortClass = 'price'
this.priceHigh = 0
this.priceLow = 0
this.getApi()
this.goods = []
}
this.istop = !this.istop
}
if (index === 2) {
this.sortType = 'desc'
this.sortClass = 'sum'
this.priceHigh = 0
this.priceLow = 0
this.productClassId = 0
this.postage = 0
this.format = 0
this.highLottery = 0
this.getApi()
this.goods = []
}












this.checkedIndex = index
if (index === 3) {
this.cover = true
}
},
sure () {
this.cover = false
this.sortType = 'desc'
this.sortClass = 'integrated'
let arrLow = ['0', '50', '100', '150', '200']
let arrHigh = ['49', '99', '149', '199', '5000000']
this.priceHigh = arrHigh[this.chooseIndex]
this.priceLow = arrLow[this.chooseIndex]
this.productClassId = 0
if (this.isCked1 === true) {
this.postage = 1
} else {
this.postage = 0
}
if (this.isCked2 === true) {
this.format = 1
} else {
this.format = 0
}
if (this.isCked3 === true) {
this.highLottery = 1
} else {
this.highLottery = 0
}
this.getApi()
this.goods = []
},

pri (indexs) {
this.chooseIndex = indexs
},
clear () {
this.isCked1 = false
this.isCked2 = false
this.isCked3 = false
this.chooseIndex = ''
},
show (index, sellerTaskId) {

this.$router.push({ name: 'details', query: { sellerTaskId: sellerTaskId } })
},
boxScroll (pos) {
if (pos.y < 0) {
let maxScroll = this.$refs.bgNav.getBoundingClientRect().height
if (Math.abs(pos.y) <= maxScroll) {
this.$refs.bgNav.style.top = `${pos.y}px`
this.$refs.navSec.style.top = `${pos.y}px`
this.$refs.scrollContainer.style.top = `${pos.y}px`
}
this.$nextTick(() => {
this.$refs.scroll.refresh()
})
}
},
setInfo (data) {
if (!data.total) {
this.$vux.alert.show({
title: ''
})
return false
}
let arrList = []
this.maxPageSize = data.total
for (let m of data.datas) {
let goods = {
imgSrc: m.picUrl,
vip: m.isPlus,
hot: m.isRecommend,
info: m.productName,
progress: m.percent,
sellerTaskId: m.sellerTaskId,
msg: {
pric: m.price,
state: m.isMore,
zhuan: m.extraGold,
hua: m.extraGold,
baina: ''
}
}
arrList.push(goods)
}
this.goods = [...this.goods, ...arrList]

this.$nextTick(() => {
this.$refs.scroll.refresh()
this.canLoading = true
})
}
}
}



